import React,{useState} from 'react';
import './courses.css';
import CloseIcon from '../../media/icons/close.svg';
import Form from '../../components/Registration from/form';


const Courses = (props) => {

    const [buy, setBuy] = useState(false);
    return (
            <>
                            <div className="courses_card">
                            <img src={props.img} alt="course-image"></img>
                             <div className="course_text">
                                 <h3 className="course_name"> {props.name} </h3>
                                  <p className="course_descrip"> {props.descrip} </p>
                                  <p className="course_price"> {props.price} </p>
                             </div>
                             <div className="course_buy--button" onClick={() => setBuy(true)}>
                                 buy now
                             </div>
                        </div>
                        {buy?(
                            <section className="project_detail_section modal">
                            <div className="project__detail modal-main">
                                <button className="cancel-button" onClick={() => setBuy(false)}> 
                                   <img style={{width:"60%"}} src={CloseIcon}></img>
                                </button>
                                  <Form course={props}/>
                              </div> 
                            </section>
                        ):null}
            </>
    )
};

export default Courses;