import React from 'react';
import './index.css'
import Logo from '../../media/logo.png';
import Menu from '../../media/icons/hamburger.svg';

const Navbar = () => {
    return (
        <div>
           <nav>
      <input type="checkbox" id="check" />
      <label for="check" className="checkbtn">
        <div className="button_div">
        <img className="nav_menu" src={Logo} alt="logo"></img>
        <h3 className="nav_menu--text" >Mkbang classes</h3>
        </div>
      </label>
      <label className="logo">
        <img className="logo-img" style={{width:"65px"}} src={Logo} alt="logo"></img>
      </label>
      <ul>
        <li><a className="active nav-links" href="/">Home</a></li>
        <li><a className="nav-links"href="#about">About</a></li>
        <li><a className="nav-links" href="#courses">Courses</a></li>
        <li><a className="nav-links" href="#faculty">Faculty</a></li>
        <li><a className="nav-links" href="#feedback">Feedback</a></li>
      </ul>
    </nav>
        </div>
    )
}

export default Navbar;